/* ============================================
   КОМПАКТНАЯ ГЛАВНАЯ СТРАНИЦА
   ============================================ */

.home-page {
  animation: fadeIn 0.5s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;  
}

/* Герой секция */
.hero-section {
  position: relative;
  padding: 80px 0 60px;
  border-radius: 20px;
  overflow: hidden;
  background: linear-gradient(135deg, 
    rgba(13, 17, 23, 0.95),
    rgba(22, 27, 34, 0.95));
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.hero-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  pointer-events: none;
}

.hero-circle {
  position: absolute;
  border-radius: 50%;
  filter: blur(60px);
  opacity: 0.4;
}

.circle-1 {
  width: 300px;
  height: 300px;
  background: linear-gradient(135deg, #ff6b6b, #ffa726);
  top: -150px;
  left: -150px;
}

.circle-2 {
  width: 250px;
  height: 250px;
  background: linear-gradient(135deg, #4ecdc4, #44a08d);
  bottom: -100px;
  right: -100px;
}

.hero-content {
  position: relative;
  z-index: 2;
  text-align: center;
  max-width: 800px;
  margin: 0 auto;
}

.hero-title {
  font-size: 3rem;
  margin-bottom: 20px;
  color: var(--text-primary);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
  flex-wrap: wrap;
}

.gradient-text {
  background: linear-gradient(135deg, 
    var(--accent-primary), 
    var(--accent-secondary));
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

.hero-subtitle {
  font-size: 1.2rem;
  color: var(--text-secondary);
  line-height: 1.6;
  margin-bottom: 40px;
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
}

.hero-actions {
  display: flex;
  justify-content: center;
}

.glass-button {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: white;
  padding: 14px 28px;
  border-radius: 12px;
  cursor: pointer;
  font-size: 1.1rem;
  transition: all var(--transition-medium);
  display: inline-flex;
  align-items: center;
  gap: 10px;
  text-decoration: none;
  font-weight: 600;
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.glass-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
  transition: left 0.7s ease;
  z-index: -1;
}

.glass-button:hover::before {
  left: 100%;
}

.glass-button:hover {
  background: rgba(255, 255, 255, 0.15);
  transform: translateY(-2px);
  box-shadow: var(--shadow-medium);
}

.primary-btn {
  background: linear-gradient(135deg, var(--accent-primary), var(--accent-secondary));
  border: none;
}

/* Информационные карточки */
.info-section {
  padding: 60px 0;
}

.info-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 25px;
}

.info-card {
  padding: 30px;
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all var(--transition-medium);
}

.info-card:hover {
  transform: translateY(-5px);
  border-color: var(--accent-primary);
  box-shadow: var(--shadow-medium);
}

.info-icon {
  font-size: 2rem;
  color: var(--accent-primary);
  margin-bottom: 20px;
}

.info-card h3 {
  font-size: 1.3rem;
  margin-bottom: 15px;
  color: var(--text-primary);
}

.info-card p {
  color: var(--text-secondary);
  line-height: 1.6;
  font-size: 0.95rem;
}

/* Уроки */
.lessons-section {
  padding: 60px 0;
}

.section-title {
  font-size: 2rem;
  margin-bottom: 30px;
  color: var(--text-primary);
  display: flex;
  align-items: center;
  gap: 12px;
}

.lessons-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
}

.lesson-card {
  padding: 25px;
  border-radius: 12px;
  text-decoration: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all var(--transition-medium);
  border: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(255, 255, 255, 0.03);
}

.lesson-card:hover {
  background: rgba(255, 255, 255, 0.05);
  transform: translateY(-3px);
  box-shadow: var(--shadow-light);
}

.lesson-content {
  flex: 1;
}

.lesson-title {
  font-size: 1.3rem;
  margin-bottom: 8px;
  color: var(--text-primary);
}

.lesson-description {
  color: var(--text-secondary);
  font-size: 0.9rem;
  margin-bottom: 15px;
}

.lesson-link {
  color: var(--accent-primary);
  font-size: 0.9rem;
  display: flex;
  align-items: center;
  gap: 6px;
}

.lesson-icon {
  font-size: 2rem;
  color: var(--accent-primary);
  margin-left: 20px;
  opacity: 0.7;
}

/* Телеграм каналы */
.channels-section {
  padding: 60px 0;
}

.section-subtitle {
  font-size: 1.1rem;
  color: var(--text-secondary);
  text-align: center;
  margin-bottom: 30px;
}

.channels-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
}

.channel-card {
  padding: 25px;
  border-radius: 12px;
  text-decoration: none;
  color: inherit;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all var(--transition-medium);
  border: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(255, 255, 255, 0.03);
}

.channel-card:hover {
  background: rgba(255, 255, 255, 0.05);
  transform: translateY(-3px);
  box-shadow: var(--shadow-light);
}

.channel-header {
  display: flex;
  align-items: center;
  gap: 15px;
}

.channel-logo {
  width: 50px;
  height: 50px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  flex-shrink: 0;
}

.channel-name {
  font-size: 1.2rem;
  margin-bottom: 5px;
  color: var(--text-primary);
}

.channel-desc {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.channel-action {
  color: #0088cc;
  font-size: 1.3rem;
  margin-left: 15px;
}

/* Рекламный баннер */
.ad-section {
  padding: 40px 0;
}

.ad-banner {
  background: linear-gradient(135deg, 
    rgba(157, 80, 187, 0.1),
    rgba(255, 107, 107, 0.05));
  border-radius: 16px;
  padding: 30px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.ad-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
}

.ad-text h3 {
  font-size: 1.4rem;
  margin-bottom: 8px;
  color: var(--text-primary);
}

.ad-text p {
  color: var(--text-secondary);
  font-size: 0.95rem;
}

.ad-btn {
  background: linear-gradient(135deg, #ff6b6b, #ffa726);
  border: none;
  padding: 12px 24px;
}

/* CTA секция */
.cta-section {
  padding: 60px 0;
}

.cta-content {
  text-align: center;
  padding: 40px;
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(255, 255, 255, 0.03);
  max-width: 600px;
  margin: 0 auto;
}

.cta-content h2 {
  font-size: 1.8rem;
  margin-bottom: 15px;
  color: var(--text-primary);
}

.cta-content p {
  color: var(--text-secondary);
  margin-bottom: 25px;
  font-size: 1.1rem;
}

.cta-btn {
  background: linear-gradient(135deg, var(--accent-primary), var(--accent-secondary));
  border: none;
  padding: 14px 30px;
}

/* Футер */
.home-footer {
  padding: 40px 0;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(13, 17, 23, 0.9);
}

.footer-content {
  text-align: center;
}

.footer-info h3 {
  font-size: 1.5rem;
  margin-bottom: 15px;
  color: var(--text-primary);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.footer-info p {
  color: var(--text-secondary);
  max-width: 500px;
  margin: 0 auto 25px;
  line-height: 1.6;
}

.footer-links {
  display: flex;
  justify-content: center;
  gap: 25px;
  margin-bottom: 30px;
  flex-wrap: wrap;
}

.footer-links a {
  color: var(--accent-primary);
  text-decoration: none;
  font-size: 1rem;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: color var(--transition-fast);
}

.footer-links a:hover {
  color: var(--accent-secondary);
}

.footer-note {
  color: var(--text-tertiary);
  font-size: 0.9rem;
}

.heart-icon {
  color: #ff6b6b;
  margin: 0 5px;
}

.copyright {
  margin-top: 10px;
  color: var(--text-tertiary);
  font-size: 0.85rem;
}

.channel-logo {
    width: 60px;
    height: 60px;
    border-radius: 12px;
    overflow: hidden;
    margin-right: 15px;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
}

.channel-logo-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

/* Мобильная адаптация */
@media (max-width: 768px) {
  .hero-section {
    padding: 60px 0 40px;
  }
  
  .hero-title {
    font-size: 2.2rem;
    flex-direction: column;
    gap: 10px;
  }
  
  .hero-subtitle {
    font-size: 1.1rem;
    padding: 0 10px;
  }
  
  .section-title {
    font-size: 1.7rem;
    justify-content: center;
  }
  
  .info-cards,
  .lessons-grid,
  .channels-grid {
    grid-template-columns: 1fr;
  }
  
  .info-card,
  .lesson-card,
  .channel-card {
    padding: 20px;
  }
  
  .ad-content {
    flex-direction: column;
    text-align: center;
  }
  
  .cta-content {
    padding: 30px 20px;
  }
  
  .footer-links {
    flex-direction: column;
    gap: 15px;
  }
}

@media (max-width: 480px) {
  .hero-title {
    font-size: 1.8rem;
  }
  
  .glass-button {
    width: 100%;
    justify-content: center;
  }
}