.menu-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(5px);
  z-index: 999;
  animation: fadeIn 0.3s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.menu-container {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  max-width: 600px;
  background: var(--bg-secondary);
  border-left: 1px solid var(--glass-border);
  z-index: 1000;
  transform: translateX(100%);
  transition: transform 0.3s ease-out;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.menu-container.open {
  transform: translateX(0);
}

.menu-header {
  padding: 16px 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.08);
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: var(--bg-tertiary);
}

.menu-title {
  font-size: 1.1rem;
  display: flex;
  align-items: center;
  gap: 10px;
  color: var(--text-primary);
  font-weight: 600;
}

.menu-title svg {
  color: var(--accent-primary);
  font-size: 1rem;
}

/* Новая кнопка закрытия */
.menu-close-btn {
  width: 44px;
  height: 44px;
  border: none;
  background: rgba(255, 107, 107, 0.2);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  position: relative;
  transition: all 0.3s ease;
  padding: 0;
  margin: 0;
  box-shadow: 0 4px 15px rgba(255, 107, 107, 0.2);
  z-index: 1001;
}

.menu-close-btn:hover {
  background: rgba(255, 107, 107, 0.3);
  transform: scale(1.1);
  box-shadow: 0 6px 20px rgba(255, 107, 107, 0.4);
}

.menu-close-btn:active {
  transform: scale(0.95);
}

/* Кастомная иконка крестика */
.menu-close-icon {
  position: relative;
  width: 20px;
  height: 20px;
  display: block;
}

.menu-close-icon::before,
.menu-close-icon::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 2px;
  background: var(--text-primary);
  border-radius: 1px;
  transition: all 0.3s ease;
}

.menu-close-icon::before {
  transform: translateY(-50%) rotate(45deg);
}

.menu-close-icon::after {
  transform: translateY(-50%) rotate(-45deg);
}

.menu-close-btn:hover .menu-close-icon::before {
  transform: translateY(-50%) rotate(135deg);
  background: var(--accent-error);
}

.menu-close-btn:hover .menu-close-icon::after {
  transform: translateY(-50%) rotate(-135deg);
  background: var(--accent-error);
}

/* Анимация при нажатии */
.menu-close-btn:active .menu-close-icon::before,
.menu-close-btn:active .menu-close-icon::after {
  transform: translateY(-50%) rotate(0);
  opacity: 0.7;
}

.menu-content {
  flex: 1;
  overflow-y: auto;
  padding: 16px;
}

.lessons-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 10px;
}

.lesson-card {
  position: relative;
  display: flex;
  align-items: flex-start;
  gap: 14px;
  padding: 14px;
  text-decoration: none;
  color: var(--text-primary);
  transition: all var(--transition-fast);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.02);
  min-height: 86px;
}

.lesson-card.available {
  cursor: pointer;
}

.lesson-card.available:hover {
  background: rgba(255, 255, 255, 0.04);
  border-color: var(--accent-primary);
  transform: translateX(3px);
}

.lesson-card.unavailable {
  cursor: not-allowed;
  opacity: 0.75;
}

.lesson-icon {
  font-size: 1.2rem;
  color: var(--accent-primary);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 38px;
  height: 38px;
  background: rgba(78, 205, 196, 0.1);
  border-radius: 8px;
  flex-shrink: 0;
  margin-top: 2px;
}

.unavailable-icon {
  opacity: 0.6;
  background: rgba(255, 255, 255, 0.03);
}

.lesson-info {
  flex: 1;
  min-width: 0;
  padding-right: 70px;
}

.lesson-title {
  font-size: 0.55rem;
  font-weight: 600;
  margin-bottom: 4px;
  color: var(--text-primary);
  line-height: 1.3;
  letter-spacing: 0.01em;
}

.unavailable-title {
  color: var(--text-tertiary);
}

.lesson-description {
  font-size: 0.75rem;
  color: var(--text-secondary);
  margin-bottom: 6px;
  line-height: 1.3;
  opacity: 0.9;
}

.unavailable-description {
  color: var(--text-tertiary);
  opacity: 0.8;
}

.lesson-tags {
  display: flex;
  gap: 5px;
  flex-wrap: wrap;
}

.lesson-tag {
  background: rgba(255, 255, 255, 0.05);
  padding: 2px 6px;
  border-radius: 12px;
  font-size: 0.65rem;
  color: var(--accent-primary);
  border: 1px solid rgba(78, 205, 196, 0.15);
  font-weight: 500;
}

.unavailable-tag {
  opacity: 0.6;
  background: rgba(255, 255, 255, 0.02);
  border-color: rgba(255, 255, 255, 0.08);
  color: var(--text-tertiary);
}

.lesson-status {
  position: absolute;
  top: 10px;
  right: 10px;
  display: flex;
  align-items: center;
  gap: 4px;
  font-size: 0.7rem;
  font-weight: 600;
  padding: 3px 8px;
  border-radius: 10px;
  z-index: 2;
  white-space: nowrap;
}

.available-status {
  background: rgba(38, 162, 105, 0.15);
  color: var(--accent-success);
  border: 1px solid rgba(38, 162, 105, 0.25);
}

.unavailable-status {
  background: rgba(255, 167, 38, 0.1);
  color: var(--accent-warning);
  border: 1px solid rgba(255, 167, 38, 0.2);
}

.lesson-status svg {
  font-size: 0.65rem;
}

/* Мобильная адаптация */
@media (max-width: 768px) {
  .menu-header {
    padding: 14px 16px;
  }
  
  .menu-content {
    padding: 14px;
  }
  
  .lesson-card {
    padding: 12px;
    gap: 12px;
    min-height: 80px;
  }
  
  .lesson-icon {
    width: 34px;
    height: 34px;
    font-size: 1.1rem;
    margin-top: 1px;
  }
  
  .lesson-info {
    padding-right: 65px;
  }
  
  .lesson-title {
    font-size: 0.82rem;
  }
  
  .lesson-description {
    font-size: 0.72rem;
  }
  
  .lesson-status {
    top: 8px;
    right: 8px;
    font-size: 0.68rem;
    padding: 2px 6px;
  }
  
  .lesson-tag {
    font-size: 0.62rem;
    padding: 2px 5px;
  }
  
  .menu-close-btn {
    width: 48px;
    height: 48px;
    border-radius: 14px;
  }
  
  .menu-close-icon {
    width: 22px;
    height: 22px;
  }
  
  .menu-close-icon::before,
  .menu-close-icon::after {
    height: 3px;
  }
  
  .menu-close-btn:active {
    background: rgba(255, 107, 107, 0.3);
    transform: scale(0.9);
  }
}

@media (max-width: 480px) {
  .menu-title {
    font-size: 1rem;
    gap: 8px;
  }
  
  .menu-close-btn {
    width: 46px;
    height: 46px;
  }
  
  .menu-close-icon {
    width: 20px;
    height: 20px;
  }
  
  .lesson-card {
    flex-direction: row;
    align-items: flex-start;
    min-height: 76px;
  }
  
  .lesson-info {
    padding-right: 60px;
  }
  
  .lesson-title {
    font-size: 0.8rem;
  }
  
  .lesson-description {
    font-size: 0.7rem;
  }
  
  .lesson-status {
    position: absolute;
    top: 8px;
    right: 8px;
    font-size: 0.66rem;
  }
  
  /* Улучшение для маленьких экранов */
  .lesson-tag {
    font-size: 0.6rem;
    padding: 1px 4px;
  }
}

/* Для горизонтальной ориентации */
@media (max-width: 768px) and (orientation: landscape) {
  .menu-container {
    max-width: 500px;
  }
  
  .lessons-grid {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .lesson-card {
    min-height: 90px;
  }
}

/* Анимация для переполнения */
.menu-content::-webkit-scrollbar {
  width: 6px;
}

.menu-content::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 3px;
}

.menu-content::-webkit-scrollbar-thumb {
  background: var(--accent-primary);
  border-radius: 3px;
}

.menu-content::-webkit-scrollbar-thumb:hover {
  background: var(--accent-secondary);
}

/* Поддержка темной темы */
@media (prefers-color-scheme: dark) {
  .menu-container {
    background: var(--bg-secondary);
    border-left-color: rgba(255, 255, 255, 0.1);
  }
  
  .menu-header {
    background: var(--bg-tertiary);
  }
  
  .menu-close-btn {
    background: rgba(255, 107, 107, 0.2);
  }
}