.header {
  background: rgba(22, 27, 34, 0.85);
  border-bottom: 1px solid rgba(255, 255, 255, 0.08);
  padding: 15px 0;
  position: sticky;
  top: 0;
  z-index: 1000;
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 60px;
}

/* Логотип - делаем выше и выравниваем с текстом */
.logo-link {
  text-decoration: none;
  display: flex;
  align-items: center;
  gap: 15px;
  transition: opacity var(--transition-fast);
  height: 100%;
}

.logo-link:hover {
  opacity: 0.9;
}

.logo-text {
  font-family: 'Cherry Bomb One', cursive;
  font-size: 3.5rem;
  font-weight: 400;
  line-height: 1;
  color: var(--text-primary);
  letter-spacing: 1px;
  display: flex;
  align-items: center;
  height: 48px;
}

.logo-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 48px;
}

.logo-name {
  font-family: var(--font-primary);
  font-size: 1.4rem;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 2px;
  line-height: 1.2;
}

.logo-subtitle {
  font-family: var(--font-primary);
  font-size: 0.9rem;
  color: var(--text-secondary);
  line-height: 1.2;
}

.header-nav {
  display: flex;
  align-items: center;
  gap: 12px;
}

/* Кнопки одинакового размера */
.glass-button {
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(12px);
  border: 1px solid rgba(255, 255, 255, 0.12);
  color: var(--text-primary);
  padding: 12px 24px;
  border-radius: 10px;
  cursor: pointer;
  font-size: 1rem;
  transition: all var(--transition-medium);
  display: inline-flex;
  align-items: center;
  gap: 10px;
  text-decoration: none;
  font-weight: 500;
  position: relative;
  overflow: hidden;
  z-index: 1;
  height: 44px;
  min-width: 140px;
  justify-content: center;
}

.glass-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
  transition: left 0.7s ease;
  z-index: -1;
}

.glass-button:hover::before {
  left: 100%;
}

.glass-button:hover {
  background: rgba(255, 255, 255, 0.12);
  transform: translateY(-2px);
  box-shadow: var(--shadow-medium);
  border-color: rgba(255, 255, 255, 0.2);
}

.glass-button:active {
  transform: translateY(0);
}

.telegram-button {
  background: linear-gradient(135deg, 
    rgba(0, 136, 204, 0.15), 
    rgba(0, 136, 204, 0.1));
  border-color: rgba(0, 136, 204, 0.2);
}

.telegram-button:hover {
  background: linear-gradient(135deg, 
    rgba(0, 136, 204, 0.2), 
    rgba(0, 136, 204, 0.15));
}

.menu-toggle {
  background: rgba(157, 80, 187, 0.1);
  border-color: rgba(157, 80, 187, 0.2);
}

.menu-toggle:hover {
  background: rgba(157, 80, 187, 0.15);
}

/* Планшеты и маленькие десктопы */
@media (max-width: 1024px) {
  .logo-text {
    font-size: 3rem;
    height: 42px;
  }
  
  .logo-info {
    height: 42px;
  }
  
  .logo-name {
    font-size: 1.2rem;
  }
  
  .logo-subtitle {
    font-size: 0.85rem;
  }
}

/* Мобильные устройства */
@media (max-width: 768px) {
  .header {
    padding: 12px 0;
  }
  
  .header-content {
    min-height: 56px;
  }
  
  .logo-text {
    font-size: 2.8rem;
    height: 44px;
  }
  
  .logo-info {
    display: none;
  }
  
  /* Увеличиваем кнопки для лучшей кликабельности */
  .header-nav {
    gap: 10px;
  }
  
  .glass-button {
    height: 50px; /* Увеличили с 46px до 50px */
    min-width: 56px; /* Увеличили с 50px до 56px */
    padding: 0;
    justify-content: center;
    border-radius: 12px;
    font-size: 1rem;
  }
  
  /* Кнопки с иконками и текстом */
  .telegram-button,
  .menu-toggle {
    width: 58px; /* Увеличили с 54px до 58px */
    height: 50px; /* Увеличили с 46px до 50px */
  }
  
  /* Скрываем текст в кнопках на мобильных */
  .telegram-button span,
  .menu-toggle span {
    display: none;
  }
  
  /* Увеличиваем иконки внутри кнопок */
  .telegram-button svg,
  .menu-toggle svg {
    font-size: 1.4rem; /* Увеличили с 1.3rem до 1.4rem */
  }
  
  /* Ховер эффекты для мобильных */
  .glass-button:active {
    background: rgba(255, 255, 255, 0.15);
    transform: scale(0.95);
  }
}

/* Очень маленькие экраны */
@media (max-width: 480px) {
  .logo-text {
    font-size: 2.6rem;
    height: 42px;
  }
  
  .header-content {
    min-height: 54px;
  }
  
  .glass-button {
    height: 48px; /* Увеличили с 44px до 48px */
    min-width: 54px; /* Увеличили с 48px до 54px */
  }
  
  .telegram-button,
  .menu-toggle {
    width: 54px; /* Увеличили с 50px до 54px */
    height: 48px; /* Увеличили с 44px до 48px */
  }
  
  .telegram-button svg,
  .menu-toggle svg {
    font-size: 1.3rem; /* Увеличили с 1.2rem до 1.3rem */
  }
}

/* Супер маленькие экраны (меньше 360px) */
@media (max-width: 360px) {
  .logo-text {
    font-size: 2.3rem;
    height: 40px;
  }
  
  .header-content {
    min-height: 52px;
  }
  
  .header-nav {
    gap: 8px;
  }
  
  .glass-button {
    height: 46px; /* Увеличили с 42px до 46px */
    min-width: 50px; /* Увеличили с 46px до 50px */
    border-radius: 12px;
  }
  
  .telegram-button,
  .menu-toggle {
    width: 50px; /* Увеличили с 46px до 50px */
    height: 46px; /* Увеличили с 42px до 46px */
  }
  
  .telegram-button svg,
  .menu-toggle svg {
    font-size: 1.2rem; /* Увеличили с 1.1rem до 1.2rem */
  }
}

/* Минимальные размеры для тач-устройств */
@media (max-width: 768px) {
  .glass-button {
    touch-action: manipulation;
  }
  
  /* Убеждаемся, что кнопки достаточно большие для пальцев */
  .glass-button {
    min-height: 44px;
  }
  
  /* Акцент на активном состоянии для лучшей обратной связи */
  .glass-button:active {
    background: rgba(255, 255, 255, 0.18);
    transition: background 0.1s ease, transform 0.1s ease;
  }
}